  $resultsContent: `Tulossivulla esitetn vertailu 'keskiarvosuomalaiseen'. Keskivertosuomalaisen kotitalouksien kulutusmenoihin perustuva hiilijalanjlki on noin 10 tonnia vuodessa.  Tst kolmannes syntyy asumisesta (rakennus, energiankytt kotona, kodin tavarat), liikenne ja ruoka muodostavat kumpikin noin viidenneksen ja loput pstt syntyvt erilaisten tavaroiden ja palveluiden kulutuksesta. Kotitalouksien kulutusmenot eivt kata kaikkia yhteiskunnan ja kolmannen sektorin yksilille tarjoamia palveluja, esimerkiksi koulutusta, terveydenhuoltoa ja pivhoitoa (ks. mys Johdanto).

  Keskivertosuomalaisen hiilijalanjlki perustuu ENVIMAT-laskentamallilla laskettuihin kulutusluokkakohtaisiin kasvihuonekaasuintensiteetteihin per kulutettu euro (Seppl ym. 2009). Laskenta on tehty vuoden 2013 kansantalouden tiedoista. ENVIMAT-malli on Suomen talouden ympristlaajennettu panos-tuotosmalli. Malli on rakennettu Oulun yliopiston Thule-instituutin ja Suomen ympristkeskuksen yhteistyn (Seppl ym. 2009). Malli on yksityiskohtainen: tuotantotoiminta on jaettu 147 toimialaan ja 232 tuotteeseen ja kotitalouksien kulutusmenot 66 kulutushydykeryhmn.

  ENVIMAT-mallissa huomioidaan kotimaassa tuotetut ja tuontituotteet. Tuontituotteiden elinkaariset ympristkuormitukset perustuvat pasiassa kansainvlisen LCIA datapankin, Ecoinventin, tietoihin. Nist johdetaan edelleen ostajahintaiset kulutushydykkeiden ympristkuormitukset, jotka sisltvt mys kaupan ja jakelukuljetusten vaikutukset.

  Vuoden 2016 Ilmastodieetin pivityksess otettiin huomioon ENVIMAT-mallin kattavuutta parantavat uudistukset (Salo ym.2016). Tavaroiden ja palveluiden pstt sisltvt aiemmasta poiketen mys tuotantoon tarvittavat koneet ja laitteet. Muutos vaikuttaa mys verrokkina kytettvn keskivertosuomalaisen hiilijalanjlkeen, joka on korkeampi kuin laskurin aiemmissa versioissa.

  Keskivertosuomalaisen lisksi vertailun voi tehd mys seuraavien vastaajajoukkojen kanssa (vertailuarvoa ei nytet jos vertailukelpoisia vastauksia on vhemmn kuin 15):
  <ul><li>Laskurin tyttneiden keskiarvo (alkaen laskurin uudistuksesta maaliskuussa 2019)</li>  <li>Vastaajat, joilla on sama postinumero</li>  <li>Vastaajat, joilla on samankokoinen perhe</li>  <li>Vastaajat, joiden bruttotuloluokka on sama,</li>  <li>Vastaajat, joilla on sama ruokavalio.</li></ul>`,
  $analysisTitle: `Mit hiilijalanjlkiluku kertoo?`,
  $analysisContent: `Hiilijalanjljen laskentatulokseen tulee suhtautua arviona. Se kertoo kuitenkin suuruusluokista: Mitk ovat oman arkeni isoimmat pstlhteet? Hiilijalanjljen pienentminen kannattaa aloittaa heti helpoista teoista kuten sislmptilan stmisest kohtuulliseksi (korkeintaan 21 C lmmityskaudella), liikkumista jalan tai pyrll lyhyill matkoilla ja valitsemalla vhhiilisi ruokia, tuotteita ja palveluja. Monet asiat vaativat suunnitelmallista toteutusta pitkll aikavlill. Esimerkiksi kodin energiaremontti ja auton vaihto tai siit luopuminen vaativat suunnittelua. Laskurissa tuodaan esille muutamia listietolhteit niin lyhyen kuin pitkn thtimen muutosten tueksi.

  Usein kysytn, mik on hiilijalanjljen tavoitetaso johon tulisi pyrki. Pariisin ilmastosopimuksen alle 1,5 asteen lmpenemisen polun edellyttmi pstvhennyksi on ksitelty tutkimuskirjallisuudessa. Niemist ym. (2019) koosteen mukaan, vuonna 2030 pstjen tulisi olla enintn noin 3000 kg per henkil ja vuonna 2050 noin 600 kg per henkil (ks. tarkemmin taulukko 11 Niemistn ym. raportissa). Vaikka psttaso on esitetty tasolla kg per henkil, ei vastuu pstvhennyksist ole kokonaan yksittisill ihmisill ja heidn valinnoillaan. Tulee mys muistaa, ett Ilmastodieetin laskentatulos kaikkia kotitalouksien kyttmi palveluita, esimerkiksi julkista terveydenhuoltoa ja koulutusta. Pstlaskennan ksitteit ja rajauksia ksiteltiin lyhyesti tmn dokumentin johdannossa ja niihin voi tutustua tarkemmin raportissa Seppl ym. 2009. Laskennan rajauksilla on merkityst silloin, kun henkil kohden laskettua tulosta verrataan tavoitetasoon tai edell esitettyyn 1,5 asteen polun tavoitetasoon.

  Pstvhennysten saavuttamiseksi tarvitaan suuria muutoksia esimerkiksi energiajrjestelmiss: kuinka energiaa tuotetaan, kulutetaan, varastoidaan ja siirretn. Kansalaiset voivat vaikuttaa kasvihuonekaasupstihin omalla arjen toiminnallaan, luomalla kysynt vhhiilisille ratkaisuille ja tukemalla kunnianhimoista ilmastopolitiikkaa.`,
